* {
  box-sizing: border-box;
}

body{
  text-align: center;
  font-family: 'Helvetica';
}

.App{
  position: relative;
  display: flex;
  max-width: 800px;
  margin: auto;
  height: 90vh;
  justify-content: center;
  flex-direction: column;
  transition: 0.3s;
  padding: 10px;
  text-align: center;
  img{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: auto;
    @media only screen and (min-width: 1100px){
      width: 100%;
      height: auto;
    }
  }
  h1 {
    width: 100%;
    font-size: 15px;
    color: white;
    font-weight: 100;
    margin: 20px auto;
    z-index: 1000;
  }
}

h2{
  margin-bottom: 30px;
  color: white;
}

h3{
  font-weight: 300;
  color: white;
}
svg{
  fill: white;
}

p{
  color: white;
  font-weight: 300;
}